.item{
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    width: 30vw;
    height: 400px;
    background: linear-gradient(0deg, rgba(28,58,89,1) 0%, rgba(9,42,74,1) 100%);
    border-radius: 1vw;
}

.item1{
    background: linear-gradient(0deg, rgb(27, 55, 83) 0%, rgba(9,42,74,1) 100%);
}

@media(max-width: 1023px){
    .item{
        width: 45vw;
        height: 400px;
        border-radius: 2vw;
    }
}

@media(max-width: 567px){
    .item{
        width: 80vw;
        height: 600px;
        border-radius: 2vw;
    }
}